function handleSubmit(event) {
    event.preventDefault()

    let formUrl = document.getElementById('name').value

    console.log("...Url Processing....")

    fetch("/link", {
        method: "POST",
        mode: "cors",
        headers: {"Content-Type" : "application/json"},
        body: JSON.stringify({formUrl})
    })
    .then(res => res.json())
    .then(function(res) {
        document.getElementById('url_polarity').innerHTML = res.polarity;
        document.getElementById('url_polarity_confidence').innerHTML = res.polarity_confidence;
        document.getElementById('url_subjectivity').innerHTML =  res.subjectivity;
        document.getElementById('url_subjectivity_confidence').innerHTML = res.subjectivity_confidence;
        document.getElementById("excerpt").innerHTML = res.text; 
        console.log(res);
    })
}


function textSubmit(event) {
    event.preventDefault()

    let formText = document.getElementById('text').value

    console.log("...Text Processing....")

    fetch("/data", {
        method: "POST",
        mode: "cors",
        headers: {"Content-Type" : "application/json"},
        body: JSON.stringify({formText})
    })
    .then(res => res.json())
    .then(function(res) {
        document.getElementById('polarity').innerHTML = res.polarity;
        document.getElementById('polarity_confidence').innerHTML = res.polarity_confidence;
        document.getElementById('subjectivity').innerHTML =  res.subjectivity;
        document.getElementById('subjectivity_confidence').innerHTML = res.subjectivity_confidence;
        console.log(res);
    })
}

export { handleSubmit, textSubmit}
